import config from "../../components/config/config.vue"
export default {
	data() {
		return {
							userList: [],
							/* 选择的用户下标 */
							pickerUserIndex: -1
		}
	},
	methods: {
		
		/* 列表触摸事件 */
		listTap: function(e,val){
			// var us=e.currentTarget.dataset.index
			uni.showActionSheet({
			    itemList: ['进入聊天', '删除会话', '添加关注'],
			    success: function (res) {
			        //console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					if(res.tapIndex==0){
						 uni.navigateTo({
						        url: '../user/chat?id='+val["id"]+'&name='+val["name"]
						    });
					}else if(res.tapIndex==1){
						var value = uni.getStorageSync('token');
						console.log(val["id"])
						if(value){
						  	uni.request({
						  	    url: config.httpUrl +'/api/v1/user/chatlist',
						  		method:"DELETE",
								data:{
									"rid":val["id"]
								},
						  	    header: {
						  		   'Authorization' : value,
						  	    },
						  	    success: (res) => {
						  			var jsondata=res.data
						  			if(jsondata.code=='0'){								
										config.txtshow("删除成功")
										uni.startPullDownRefresh();
						  			}else{
						  				 config.txtshow(jsondata.msg)				  				
						  			}								
						  	    },
						  		fail: () =>{  
						  			 config.txtshow('网络异常')
						  	     }, 
						  	});
						  }else{
						  	//跳转到登录界面
							console.log("需要登录")
						  	uni.redirectTo({
						  		url: '../user/login'
						  	});
						    
						}
					}else if(res.tapIndex==2){
						var value = uni.getStorageSync('token');
						uni.request({
						    url: config.httpUrl +'/api/v1/user/friend',
						    data: {
								 "oid":val["id"]
						    },
							method:"POST",
						    header: {
						       'content-type': 'application/x-www-form-urlencoded',
							   'Authorization' : value,
						    },
						    success: (res) => {
								var jsondata=res.data
								if(jsondata.code=='0'){//登录成功
									config.txtshow('关注成功')
								}else if(jsondata.code=='1'){
									config.txtshow('已经关注')
								}else{
									config.txtshow(jsondata.msg)
								}								
						    },
							fail: () =>{  
								config.txtshow('网络异常')
						     }, 
						});
						
																		
					}else{
						//其他行为
					}
			    },
			    fail: function (res) {
					//不进行任何操作
			        // console.log(res.errMsg);
			    }
			});				

			
			
		},
		/* 获取列表数据 */
		getListData() {

			config.txtshow("refresh")

			let list = [];
			//向后端发起请求
			var value = uni.getStorageSync('token');
			if(value){
			  	uni.request({
			  	    url: config.httpUrl +'/api/v1/user/chatlist',
			  		method:"GET",
			  	    header: {
			  		   'Authorization' : value,
			  	    },
			  	    success: (res) => {
			  			var jsondata=res.data
			  			if(jsondata.code=='0'){								
							for (let i = 0; i < jsondata.data.length; i++) {
								//时间
								var tt="xx.xx";
								var now=new Date(jsondata.data[i]["created_at"]*1000);
								var month = now.getMonth()+1;//得到月份
								var date = now.getDate();//得到日期
								var hour = now.getHours();//得到小时
								var minu = now.getMinutes();//得到分钟
								if (new Date(jsondata.data[i]["created_at"]*1000).toDateString() === new Date().toDateString()) {
								        //今天
										//date.getDate()< 10 ? '0'+date.getDate() :date.getDate()
								    tt=(hour< 10 ? '0'+hour : hour)+":"+(minu< 10 ? '0'+minu : minu)
								} else if (new Date(jsondata.data[i]["created_at"]*1000) < new Date()){
								        //之前
								    tt=month+"月"+date+"日"
								}
								//头像
								var photo="../../static/my/unlogin.jpg"
								if(jsondata.data[i]["avatar"]){
									photo=jsondata.data[i]["avatar"]
								}
								
								//内容
								var mess=""
								if(jsondata.data[i]["type"]==0){
									mess=jsondata.data[i]["last_content"]
								}else if(jsondata.data[i]["type"]==1){
									mess="[图片]"
								}
								
								list.push({
									"id":jsondata.data[i]["id"],
									"name":jsondata.data[i]["nickname"],
									"time":tt,
									"info":mess,
									"type":jsondata.data[i]["type"],
									"avatar":photo
									})
								}
							this.userList = list;								
			  			}else{
			  				 config.txtshow(jsondata.msg)				  				
			  			}								
			  	    },
			  		fail: () =>{  
			  			 config.txtshow('网络异常')
			  	     }, 
			  	});
			  }else{
			  	//跳转到登录界面
			  	uni.navigateTo({
			      url: '../../pages/user/login'
			  });
			    console.log("需要登录")
			}
			
			
		},
	},
	onLoad() {
		uni.startPullDownRefresh();	
	},
	onTabItemTap(){
		uni.startPullDownRefresh();
	},
	onPullDownRefresh() {
			this.getListData();	
	        setTimeout(function () {
	            uni.stopPullDownRefresh();
	        }, 1000);
	    }
}
